import React, { useEffect, useState } from 'react';
import { Row, Col, ListGroup, ProgressBar } from 'react-bootstrap'
import axios from 'axios';
import { Pagination } from 'react-bootstrap'
import moment from 'moment'

import { EVENTS_API_URL } from '../../store/config'

const DataSource = axios.create({
  baseURL: EVENTS_API_URL
})

const PaginationWrapper = ({ postsPerPage, totalPosts, paginate }) => {
  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalPosts / postsPerPage); i++) {
    pageNumbers.push(i);
  }

  // return (
  //   <nav>
  //     <ul className="paginate">
  //       {pageNumbers.map((num) => (
  //         <li key={num}>
  //           <a onClick={() => paginate(num)} href='!#' >
  //             {num}
  //           </a>
  //         </li>
  //       ))}
  //     </ul>
  //   </nav>
  // );
  return (
    <Pagination>
      {pageNumbers}
    </Pagination>
  )
};

const Posts = ({ posts, isLoading }) => {


  if (isLoading) {
    return <h2>Loading...</h2>;
  }
 
  return (

    <Row>
    <Col>
      <ListGroup variant="flush">
        {
          posts.map((e, i) => {

            let dt0 = moment(e.startDt)
            let dt1 = moment(e.endDt);
            // let hasData = e.data.length > 0;
            // let pct = (e.hours / this.props.maxHours) * 100
            let pct = (e.hours / 48) * 100

            return (
              <ListGroup.Item
                action
                // onClick={() => this.handleListClick(e.eventid)}
                key={i}
                eventKey={e.eventid}
                // variant={hasData ? "primary" : ""}
                className="event-list-item"
              >
                <small>
                  <Row>
                    <Col xs={7}>
                      {dt0.format("DD MMM YYYY, h:mm a")} &mdash; {dt1.format("DD MMM YYYY, h:mm a")}&nbsp;
                      </Col>
                    <Col xs={5}>
                      {e.hours} hour{(e.hours > 1) ? "s" : ""}
                    </Col>
                    {/* <Col xs={2}>
                      {e.isFetching ? <FontAwesomeIcon icon={faSpinner} pulse/> : ("")}
                      {hasData ? <FontAwesomeIcon icon={faCloudRain}/> : ("")}
                      </Col> */}
                  </Row>
                </small>
                <Row>
                  <Col>
                    <ProgressBar
                      className="event-length-bar"
                      now={pct}
                      // label={`${e.hours} hours`} 
                      variant="primary"
                    />
                  </Col>
                </Row>
              </ListGroup.Item>
            )

          })
        }
      </ListGroup>
    </Col>
  </Row>

    // <ul className='post-list'>
    //   {posts.map(post => (
    //     <li key={post.id}>
    //       {post["report_label"]}
    //     </li>
    //   ))}
    // </ul>
  );
};


const PaginatedEventsList = (props) => {

  const [posts, setPosts] = useState([]);
  const [postCount, setPostCount] = useState(0);
  const [nextPage, setNextPage] = useState(null);
  const [prevPage, setPrevPage] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  const [postsPerPage] = useState(20);

  useEffect(() => {
    const fetchPosts = async () => {
      setIsLoading(true);
      const res = await DataSource.get();
      setPosts(res.data.results);
      setPostCount(res.data.count);
      setPrevPage(res.data.previous)
      setNextPage(res.data.next)
      setIsLoading(false);
    };

    fetchPosts();
  }, []);

  const lastPost = currentPage * postsPerPage;
  const firstPost = lastPost - postsPerPage;
  const currentPosts = posts.slice(firstPost, lastPost);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="paginated-events-list">
      <div>
        <Posts posts={currentPosts} loading={isLoading} />
        <PaginationWrapper
          postsPerPage={postsPerPage}
          totalPosts={posts.length}
          paginate={paginate}
        />
      </div>
    </div>
  );

};

export default PaginatedEventsList